import { Button, makeStyles, Text } from "@fluentui/react-components";
import React from "react";
import { Selector } from "./Selector";
import { Difficulty } from "./utils";

const useStyles = makeStyles({
  container: {
    display: "flex",
    flexDirection: "column",
    marginLeft: "15px",
  },
  difficultyContainer: { display: "flex" },
  difficultyButton: { marginRight: "5px !important" },
});

export const Main: React.FC = () => {
  const [ready, setReady] = React.useState(false);
  const [win, setWin] = React.useState(0);
  const [lost, setLost] = React.useState(0);
  const [lastMessage, setLastMessage] = React.useState("");
  const [difficulty, setDifficulty] = React.useState(Difficulty.easy);
  const styles = useStyles();

  const lostGame = React.useCallback(() => {
    setLost((l) => l + 1);
    setReady(false);
    setLastMessage("MGJ");
  }, []);

  const winGame = React.useCallback(() => {
    setWin((w) => w + 1);
    setReady(false);
    setLastMessage("");
  }, []);

  const startGame = React.useCallback((targetDifficulty: Difficulty) => {
    setDifficulty(targetDifficulty);
    setReady(true);
  }, []);

  return ready ? (
    <div className={styles.container}>
      <Selector
        winCallback={winGame}
        lostCallback={lostGame}
        difficulty={difficulty}
      />
    </div>
  ) : (
    <div className={styles.container}>
      <Text as="h1" size={900}>
        
      </Text>
      <Text as="h3" size={500}>
        UNN {win}:{lost}{" "}
        
      </Text>
      <Text as="p" size={400}>
        {lastMessage} <del></del>
      </Text>
      <div className={styles.difficultyContainer}>
        <Button
          className={styles.difficultyButton}
          appearance="primary"
          onClick={() => {
            startGame(Difficulty.easy);
          }}
        >
          
        </Button>
        <Button
          className={styles.difficultyButton}
          appearance="primary"
          onClick={() => {
            startGame(Difficulty.medium);
          }}
        >
          
        </Button>
        <Button
          className={styles.difficultyButton}
          appearance="primary"
          onClick={() => {
            startGame(Difficulty.normal);
          }}
        >
          
        </Button>
        <Button
          className={styles.difficultyButton}
          appearance="primary"
          onClick={() => {
            startGame(Difficulty.hard);
          }}
        >
          
        </Button>
        <Button
          className={styles.difficultyButton}
          appearance="primary"
          onClick={() => {
            startGame(Difficulty.hardPlus);
          }}
        >
          
        </Button>
        <Button
          className={styles.difficultyButton}
          appearance="primary"
          onClick={() => {
            startGame(Difficulty.impossible);
          }}
        >
          
        </Button>
      </div>
    </div>
  );
};
